import axiosInstance from '../../../utils/axios.middleware.js';

class Question {

	static getLatestFilledForms = ({ _pageNo, _count }) => {
		let apiPath = `/api/form/list-latest-filled-forms?_pageNo=${_pageNo}&_count=${_count}`;
		return axiosInstance.get(apiPath);
	};

	static getPatientEditForm = ({ id }) => {
		let apiPath = `/api/form/get-edit-demo?id=${id}&type=patient`;
		return axiosInstance.get(apiPath);
	};

	static updatePatientDemo = ({ dynamicQuestions, preDefinedQuestions, demo_id, patient_id }) => {
		let apiPath = `/api/form/update-edit-demo`;
		return axiosInstance.post(apiPath, { dynamicQuestions, preDefinedQuestions, demo_id, patient_id, type: 'patient' });
	};

	static getCaregiverEditForm = ({ id }) => {
		let apiPath = `/api/form/get-edit-demo?id=${id}&type=caregiver`;
		return axiosInstance.get(apiPath);
	};

	static updateCaregiverDemo = ({ dynamicQuestions, preDefinedQuestions, demo_id, patient_id }) => {
		let apiPath = `/api/form/update-edit-demo`;
		return axiosInstance.post(apiPath, { dynamicQuestions, preDefinedQuestions, demo_id, patient_id, type: 'caregiver' });
	};

	static getChildEditForm = ({ id }) => {
		let apiPath = `/api/form/get-edit-demo?id=${id}&type=child`;
		return axiosInstance.get(apiPath);
	};

	static updateChildDemo = ({ dynamicQuestions, preDefinedQuestions, demo_id, patient_id }) => {
		let apiPath = `/api/form/update-edit-demo`;
		return axiosInstance.post(apiPath, { dynamicQuestions, preDefinedQuestions, demo_id, patient_id, type: 'child' });
	};

	static getEditPatientScales = ({ id }) => {
		let apiPath = `/api/form/get-edit-scale?id=${id}&type=patient`;
		return axiosInstance.get(apiPath);
	};

	static updatePatientScales = ({ patient_id, scales }) => {
		let apiPath = `/api/form/update-edit-scale`;
		return axiosInstance.post(apiPath, { patient_id, scales, type: 'patient' });
	};

	static getEditCaregiverScales = ({ id }) => {
		let apiPath = `/api/form/get-edit-scale?id=${id}&type=caregiver`;
		return axiosInstance.get(apiPath);
	};

	static updateCaregiverScales = ({ patient_id, scales }) => {
		let apiPath = `/api/form/update-edit-scale`;
		return axiosInstance.post(apiPath, { patient_id, scales, type: 'caregiver' });
	};

	static getEditChildScales = ({ id }) => {
		let apiPath = `/api/form/get-edit-scale?id=${id}&type=child`;
		return axiosInstance.get(apiPath);
	};

	static updateChildScales = ({ patient_id, scales }) => {
		let apiPath = `/api/form/update-edit-scale`;
		return axiosInstance.post(apiPath, { patient_id, scales, type: 'child' });
	};

	static deleteForm = async ({ id }) => {
		let apiPath = `/api/form/delete-form?id=${id}`;
		return axiosInstance.delete(apiPath);
	};

}

export default Question;