import Form from '../services/form/form.js';
import actionErrorHandler from '../../helpers/actionErrorHandler.js';

export const getLatestFilledForms = (params) => {
	return Form.getLatestFilledForms(params)
		.then((response) => {
			return {
				status: response.status,
				data: response.data,
				message: "success",
			};
		})
		.catch((e) => {
			return actionErrorHandler(e);
		});
};

