import { initialQuestions } from "../component/Form/constants.js";


export const validateChildren = (children, noOfChildren) => {

	// i must have children.lenght that is equal to 1. However, if the noOfChildren is 0, then children.length will be 1
	// validate if noOfChildren is 0, skip the validation and return true
	if (noOfChildren === 0) {
		return true;
	}

	// validate if data is empty, return false
	if (!children) {
		return false;
	}
	// validate if data is not an array, return false
	if (!Array.isArray(children) || children.length !== noOfChildren) {
		return false;
	}

	// validate if every object of the array has the required keys
	let isValid = true;
	for (let i = 0; i < children.length; i++) {
		const child = children[i];
		if (child.age.length === 0 || child.education_level.length === 0) {
			isValid = false;
			break;
		}
	}

	return isValid;
};


export const processPatientDemoOptions = ({ data, children, noOfChildren }) => {
	return processDemoOptions({ data, children, noOfChildren, patient_id: null });
};

export const processCaregiverDemoOptions = ({ data, children, noOfChildren, patient_id }) => {
	return processDemoOptions({ data, children, noOfChildren, patient_id });
};

export const processChildDemoOptions = ({ data, patient_id }) => {

	// 1. hardcoded questions
	let initialQuestionsKeys = initialQuestions.map((question) => question.name);
	let preDefinedQuestions = initialQuestionsKeys.reduce((acc, key) => {
		acc[key] = data[key];
		return acc;
	}, {});

	// 2. dynamic questions
	const dynamicQuestions = {
		dynamicKeys: Object.keys(data).map((key) => Number(key)),
		dynamicData: data
	};
	return {
		dynamicQuestions,
		preDefinedQuestions,
		patient_id
	};


};

export const processScaleOptions = ({ patient_id, data }) => {
	const dataObject = [];
	for (const [key, value] of Object.entries(data)) {
		if (!value) continue;

		const [patient_id, section_id, question_id] = key?.split('-');
		const [option_id, is_answer_in_urdu, option_text] = value?.split('-');
		dataObject.push({
			patient_id: parseInt(patient_id),
			section_id: parseInt(section_id),
			section_question_id: parseInt(question_id),
			section_option_id: parseInt(option_id),
			answer_text: option_text,
			is_answer_in_urdu: Boolean(Number(is_answer_in_urdu)),
		});
	}

	return {
		patient_id,
		data: dataObject,
	};
};

function processDemoOptions({ data, children, noOfChildren, patient_id }) {
	// 1. hardcoded questions
	let initialQuestionsKeys = initialQuestions.map((question) => question.name);
	let preDefinedQuestions = initialQuestionsKeys.reduce((acc, key) => {
		acc[key] = data[key];
		return acc;
	}, {});
	preDefinedQuestions.no_of_children = noOfChildren;
	children.forEach((child) => {
		preDefinedQuestions.children_age = preDefinedQuestions.children_age ? preDefinedQuestions.children_age + ',' + child.age.toString() : child.age.toString();
		// in preDefinedQuestions.children_education_level, replace commas with white spaces to avoid confusion with csv
		preDefinedQuestions.children_education_level = preDefinedQuestions.children_education_level ? preDefinedQuestions.children_education_level + ',' + child.education_level?.replace(',', ' ').toString() : child.education_level?.replace(',', ' ').toString();
	});

	// 2. dynamic questions
	const dynamicQuestions = {
		dynamicKeys: Object.keys(data).map((key) => Number(key)),
		dynamicData: data
	};

	let returnObj = {
		dynamicQuestions,
		preDefinedQuestions,
	};

	if (patient_id)
		return {
			...returnObj,
			patient_id
		};
	return returnObj;
};