import Spinner from "react-bootstrap/Spinner";
import Button from "react-bootstrap/Button";
import { useNavigate } from "react-router-dom";

export const PageCard = ({ children, title, isLoading, paddingSmall, resetCookieAndLocalStorage }) => {
	const navigate = useNavigate();
	const clearFormCache = () => {
		const shouldClear = window.confirm('Are you sure you want to clear cache?');
		if (shouldClear) {
			resetCookieAndLocalStorage();
			navigate("/");
		}
	};
	return (
		<div className="my-8 card ">
			<h1 className="card-header">
				<div className="card-title m-0">
					<h3 className="fw-bolder m-0">{title}</h3>
				</div>
				{!isLoading ? null : (<div className="card-subtitle card-title m-0">
					<Spinner
						as="span"
						animation="grow"
						size="md"
						role="status"
						aria-hidden="true"
					/>

					<p className="m-0">Loading ...</p>
				</div>)}
				{resetCookieAndLocalStorage &&
					<div className="card-subtitle card-title m-0">
						<Button variant="outline-danger" className="m-0" onClick={clearFormCache} >Clear Form Cache</Button>
					</div>
				}
			</h1>
			<div className={`card-body ${isLoading ? 'opacity-50' : null}`} style={isLoading ? { pointerEvents: "none" } : {}}>
				<div className={`${paddingSmall ? 'p-sm-0 px-md-3 px-lg-10' : 'p-sm-0 px-md-10 px-lg-20'}`} >
					{children}
				</div>
			</div>
		</div>
	);
};



export const CenterTextCard = ({ children, styles }) => {
	return (
		<div className={`d-flex align-items-center justify-content-center`} style={styles} >
			<div className="text-center">
				{children}
			</div>
		</div>
	);
};

export const AlertCard = ({ children, type, message }) => {
	return (
		<>
			<div className={`alert alert-${type}`} role="alert">
				<h4 className="alert-heading">{type.replace(/^./, type[0].toUpperCase())}</h4>
				{message}
				{/* <hr /> */}
				{/* Whenever you need to, be sure to use margin utilities to keep things nice and tidy. */}
			</div>
			{children}
		</>
	);
};
