import React, { useEffect, useRef, useState } from 'react';
import Alert from 'react-bootstrap/Alert';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useForm } from 'react-hook-form';
import { useLocation, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useEditFormContext } from '../../../context/EditFormContextProvider.js';
import { useMediaQuery, useTitle } from '../../../helpers/custom-hooks';
import { processPatientDemoOptions, validateChildren } from '../../../helpers/form.helper';
import { getPatientEditForm, updatePatientDemo } from '../../../store/actions/form.action';
import { PageCard } from '../../commonComponents/Cards/HelperCards';
import { initialQuestions } from '../constants';


const Edit = () => {
	const location = useLocation();
	const navigate = useNavigate();

	const [alert, setAlert] = useState(true);
	const [isLoading, setIsLoading] = useState(false);
	const [noOfChildren, setNoOfChildren] = useState(0);
	const [children, setChildren] = useState([
		{
			age: '',
			education_level: ''
		}
	]);
	const [questions, setQuestions] = useState([]);

	const isDesktop = useMediaQuery('(min-width: 992px)');
	const { register, handleSubmit, formState: { errors }, setValue } = useForm();
	const { setEditPatientId_c, getEditNavigateTo_c, resetAllEdit_c } = useEditFormContext();
	const EDIT_NAVIGATE_TO = getEditNavigateTo_c();

	useTitle('Edit Form');
	useEffect(() => {
		window.scrollTo(0, 0);
		resetAllEdit_c();
		if (!location.state?.id) {
			navigate('/');
		} else {
			setEditPatientId_c(location.state?.id);
			_getPatientEditForm();
		}
	}, []);

	async function _getPatientEditForm() {
		const patient_id = location.state?.id;
		if (!patient_id) {
			toast.error('No ID Found');
			return navigate('/');
		}

		setIsLoading(true);
		try {
			const response = await getPatientEditForm({ id: patient_id });
			if (response.status === 200) {
				// Starts - set state for children and predefined questions
				setNoOfChildren(parseInt(response.data.patient.demographic[0].children.no_of_children));
				const children = [];
				const childrenAge = response.data.patient.demographic[0].children.children_age.split(',');
				const childrenEducationLevel = response.data.patient.demographic[0].children.children_education_level.split(',');
				for (let i = 0; i < parseInt(response.data.patient.demographic[0].children.no_of_children); i++) {
					children.push({
						age: parseInt(childrenAge[i]),
						education_level: childrenEducationLevel[i]
					});
				}
				setChildren(children);

				setValue('no_of_children', parseInt(response.data.patient.demographic[0].children.no_of_children));
				setValue('children_age', parseInt(response.data.patient.demographic[0].children.children_age));
				initialQuestions.forEach((question) => {
					if (question.type === 'date') {
						setValue(question.name, response.data.patient.demographic[0][question.name].split('T')[0]);
						return;
					}
					setValue(question.name, response.data.patient.demographic[0][question.name]);
				});
				// Ends - set state for children and predefined questions

				// set value for demo_id
				setValue('patient_id', parseInt(patient_id));
				setValue('demo_id', parseInt(response.data.patient.demographic[0].demo_section_answer[0].demo_id));

				// Starts - set state for dynamic questions
				const demoSectionQuestionsAndAnswers = response.data.patient.demographic[0].demo_section_answer.map((question, index) => {
					return {
						id: question.question.id, //  this is the demo_section_answer id, rest is related to question.
						answer_text: question.answer_text,
						question_text: question.question.question_text,
						is_nullable: question.question.is_nullable,
						question_urdu_text: question.question.question_urdu_text || "",
						radio_options: question.question.radio_options,
						is_radio_type: question.question.is_radio_type,
						is_date_type: question.question.is_date_type,
						is_number_type: question.question.is_number_type,
						is_text_type: question.question.is_text_type,
					};
				});
				setQuestions(demoSectionQuestionsAndAnswers);
				demoSectionQuestionsAndAnswers.forEach((question) => {
					setValue(`${question.id}`, question.answer_text);
				});
				// Ends - set state for dynamic questions

				setEditPatientId_c(patient_id);
				setIsLoading(false);
			} else if (response.status >= 400) {
				toast.error(response.message.message);
			}
		} catch (err) {
			toast.error(err.message);
		}
	}

	async function handleData(data) {
		// this is data after it is validated
		const patient_id = parseInt(data.patient_id);
		if (!patient_id || isNaN(patient_id)) {
			toast.error('Error processing the form, please try again later');
			return;
		}
		if (!validateChildren(children, noOfChildren)) {
			toast.error('Please fill all the fields of children');
			return;
		}
		const demo_id = parseInt(data.demo_id);
		if (!demo_id || isNaN(demo_id)) {
			toast.error('Error processing the form, please try again later');
			return;
		}
		setIsLoading(true);
		try {
			const finalOptions = processPatientDemoOptions({ data, children, noOfChildren });
			const response = await updatePatientDemo({ demo_id, patient_id, ...finalOptions });
			if (response.status === 200) {
				setEditPatientId_c(patient_id);
				navigate(EDIT_NAVIGATE_TO.caregiver);
			} else if (response.status >= 400) {
				toast.error('Some Error Occured!');
			}
		} catch (err) {
			toast.error(err.message);
		}
		setIsLoading(false);
	};

	return (
		<>
			<PageCard title="Edit Patient Demographics" isLoading={isLoading}>
				{alert ? <Alert variant="info" dismissible onClose={() => setAlert(false)} >
					<Alert.Heading>Edit Patient Demographics</Alert.Heading>
					<hr />
					<p className="mb-0">
						This is patient's data form. Please only fill patient's demographics here.
					</p>
				</Alert> : null}

				<div style={isLoading ? { pointerEvents: `none`, opacity: 0.5 } : {}}>
					<Form onSubmit={handleSubmit(handleData)}>


						{/* These are hardcoded questions. arounf 4-5 that collect general information about the target */}
						{initialQuestions.map((formGroup, index) => (
							<Form.Group className="mb-3" controlId={formGroup.name} key={index}>
								<Form.Label className='row d-flex justify-content-between'>
									<div className={`${formGroup.urdu_label.length > 0 ? 'col-md-6' : 'col-12'}`}>
										<b>
											{`${formGroup.label} : `}{formGroup.required ? <span className='text-danger'>*</span> : null}
										</b>
									</div>
									{formGroup.urdu_label.length > 0 ?
										<div className='col-md-6 text-end'>
											<b>
												{formGroup.required ? <span className='text-danger'>*</span> : null}{` : ${formGroup.urdu_label}`}
											</b>
										</div>
										: null
									}
								</Form.Label>
								<div className='px-10'>
									{formGroup.type === 'radio' ?
										<>
											<Form.Select
												key={index}
												id={`${formGroup.name}-${index}`}
												{...register(formGroup.name, { required: formGroup.required })}
											>
												<option value={``} key={0}>Select</option>
												{formGroup.radio_options.map((option, i) => (
													<option key={i + 1} value={option}>{option}</option>
												))}
											</Form.Select>
										</>
										:
										<>
											<Form.Control
												type={formGroup.type}
												min={0}
												{...register(formGroup.name, { required: formGroup.required })}
											/>
										</>
									}
								</div>
								{errors[formGroup.name] && <span className="text-danger">This field is required</span>}
							</Form.Group>
						))}


						{/* custom feilds for children data  - Start */}
						{/* first question: number of children */}
						<Form.Group className="mb-3" controlId="no_of_children">
							<Form.Label><b>{`No of children : `}<span className='text-danger'>*</span></b></Form.Label>
							<div className='px-10'>
								<Form.Control
									type='number'
									value={noOfChildren}
									onChange={(e) => {
										if (Number(e.target.value) > 0) {
											setNoOfChildren(Number(e.target.value));
											setChildren([...Array(Number(e.target.value)).keys()].map((_) => ({
												age: '',
												education_level: ''
											})));
										} else {
											toast.error('Number of children cannot be less than 1');
										}
									}
									}
									min={1}
									required
								/>
							</div>
						</Form.Group>

						{/* if number of children are greater than 0 then ask ages of every children and their education level  */}
						{noOfChildren > 0 ? children.map((_, index) => (
							<div key={index} className='row'>

								<div className='col-4'>
									<Form.Group className="mb-3" controlId="age">
										<Form.Label><b>{`Age of child ${index + 1}: `}<span className='text-danger'>*</span></b></Form.Label>
										<div className={`${isDesktop ? 'px-10' : 'px-2'}`}>
											<Form.Control
												type='text'
												onChange={(e) => {
													const newChildren = [...children];
													newChildren[index].age = e.target.value;
													setChildren(newChildren);
												}}
												value={children[index].age}
											/>
										</div>
									</Form.Group>
								</div>

								<div className='col-8'>
									<Form.Group className="mb-3" controlId="education_level">
										<Form.Label><b>{`Education level of child ${index + 1} : `}<span className='text-danger'>*</span></b></Form.Label>
										<div className={`${isDesktop ? 'px-10' : 'px-2'}`}>
											<Form.Control
												type='text'
												onChange={(e) => {
													const newChildren = [...children];
													newChildren[index].education_level = e.target.value;
													setChildren(newChildren);
												}}
												value={children[index].education_level}
											/>
										</div>
									</Form.Group>
								</div>

							</div>
						)) : null}
						{/* custom feilds for children data  - End */}



						{/* These are rest af the quesrions fetched from the database */}
						{questions.map((formGroup, index) => (
							<Form.Group className="mb-3" controlId={formGroup.id} key={index} >
								<Form.Label className='row d-flex justify-content-between'>
									<div className={`${formGroup.question_urdu_text?.length > 0 ? 'col-md-6' : 'col-12'}`}>
										<b>
											{`${formGroup.question_text} : `}{!formGroup.is_nullable ? <span className='text-danger'>*</span> : null}
										</b>
									</div>
									{formGroup.question_urdu_text?.length > 0 ?
										<div className='col-md-6 text-end'>
											<b>
												{!formGroup.is_nullable ? <span className='text-danger'>*</span> : null}{` : ${formGroup.question_urdu_text}`}
											</b>
										</div>
										: null
									}
								</Form.Label>
								<div className='px-10'>
									{
										formGroup.is_text_type ?
											<>
												<Form.Control
													type='text'
													{...register(formGroup.id?.toString(), { required: !formGroup.is_nullable })}
												/>
											</>
											:
											formGroup.is_number_type ?
												<>
													<Form.Control
														type='number'
														min={0}
														{...register(formGroup.id?.toString(), { required: !formGroup.is_nullable })}
													/>
												</>
												:
												formGroup.is_date_type ?
													<>
														<Form.Control
															type='date'
															{...register(formGroup.id?.toString(), { required: !formGroup.is_nullable })}
														/>
													</>
													:
													formGroup.is_radio_type ?
														<>

															<Form.Select
																// type='radio'
																id={`${formGroup.id}-${index}`}
																{...register(formGroup.id?.toString(), { required: !formGroup.is_nullable })}
															>
																<option value="" key='0'>Select</option>
																{formGroup.radio_options?.split(',').map((option, i) => (
																	<>
																		<option key={`default-${formGroup.id}-${i}`} value={option}>{option}</option>
																	</>
																))}
															</Form.Select>
														</>
														: null
									}
								</div>
								{errors[formGroup.id?.toString()] ? <span className="text-danger">This field is required</span> : null}
							</Form.Group>
						))}


						<Form.Group className="mb-3">
							<Button className="mb-3 float-end" variant="primary" type="submit">
								Next
							</Button>
						</Form.Group>

					</Form>
				</div>
			</PageCard>
		</>
	);
};

export default Edit;